var publicLo={
  sessionStorageSav:(key,vule)=>{
    sessionStorage.setItem(key,vule)
  },
  sessionStorageGet:(key)=>{
   return sessionStorage.getItem(key)
  },
  //
  senImg:(domId,fn)=>{
    //
    var files=document.getElementById(domId).files[0];
    //FileReader 
    var reader = new FileReader();
    //FileReaderreadAsDataURL
    reader.readAsDataURL(files);
    reader.onload =fn;
  },
  //
  timeCheng:(time)=>{
    var date = new Date(time);
    var  Y = date.getFullYear() + '-';
    var  M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
    var  D = date.getDate() + ' ';
    var  h = date.getHours() ;
    var  m = date.getMinutes();
    var  s = date.getSeconds();
    var week=date.getDay();
    week=publicLo.wek(week);
     if(m<10){
       m="0"+m.toString()
     }
    if(M<10){
      M="0"+M.toString()
    }
    if(D<10){
      D="0"+D.toString()
    }
    if(h<10){
      h="0"+h.toString()
    }
    h+=':'
    var tim={
      ymd:Y+M+D,
      hms:h+m,
      times:Y+M+D+h+m,
      s:s,
      md:M+D,
      week:week,
      y:Y
    };
  return tim
  },
  //
  dataChange:function(data){
    var date, week,nexDate,today,time;
    if(!data){
       date = new Date();
     }else{
       date = new Date(data);
     }
    //
      week = date.getDay();
    //
      time=date.getTime();
    //
      today=this.timeCheng(time);
      var ymdTiem=today.times;
      var y=today.y
      today=today.ymd
      nexDate= this.timeCheng(time+86400000);
      nexDate =nexDate.ymd;

     return {today:today,week:week,nexDate:nexDate ,time:time,ymdTiem:ymdTiem,y:y}
  },
  //
  weekDate:function(data,parm,nuber){
    "use strict";
   if(!nuber){
     nuber=7
   }
    var date,week,nexDate,time;
    date = new Date(data);
    time=date.getTime();
    var dat=[];
    for(let i=0 ;i<nuber;i++){
      var timeDat=86400000*i;
      if(parm=="add"){
        nexDate= this.timeCheng(time+timeDat);
        dat.push(nexDate);
      }
      else{
        nexDate= this.timeCheng(time-timeDat);
        dat.unshift(nexDate);
      }
    }
    return dat;
  },
  wek:(a)=>{
    "use strict";
    a=parseInt(a);
    var res;
    switch (a) {
      case 1:
        res="" ;
        break;
      case 2:
        res="" ;
        break;
      case 3:
        res="" ;
        break;
      case 4:
        res="" ;
        break;
      case 5:
        res="" ;
        break;
      case 6:
        res="" ;
        break;
      case 0:
        res="" ;
        break;
    }
    return res;
  },
  //
  calendars:()=>{
    "use strict";

  },
  //
  tmieOut(startTime,time,now){
    "use strict";
    var jinzhi=60*1000;
    var time=parseInt(time)*jinzhi;
    var now=now;
    var timeOutsNow = now-startTime;
    timeOutsNow = time-timeOutsNow;
    var timeM= parseInt(timeOutsNow/jinzhi);
    var timeS=timeOutsNow%jinzhi;
    timeS=parseInt(timeS/1000);
    return {timeM:timeM,timeS:timeS}
  },
  //
  sortData:(arr,key,up)=>{
    //arr  keykeyupfalse-true-
    arr.sort(publicLo.sortDataParm(key,up));
    return arr
  },
  //
  sortDataParm:(key,disc)=>{
    return  function (a,b){
      return disc ? a[key]<b[key]:a[key]>b[key]
    }
  }
};
//
var PatientFn={
  //
  add:(dat,fn)=>{
    //token
    var token =localStorage.getItem('access_token');
    var url='/api/organization/patients';
    this.$http.post(url,{
      headers:{
        Authorization:'bearer'+token
      },
      record:dat
    }).then(function(res){
      var res=res.body;
    })
  }

}



export default { publicLo ,PatientFn};



