const commitTooking={
  //
  lognOut:(state,data)=>{
    state.recodeList=null;
    //
    state.Odering=null;
    // 
    state.Odering={title:"",id:"",easemobName:""};
   //
      state.masge=null;
      //
      state.user=null;
      //
        state.ucli=false;
      //
      //
        state.tookenM=[];
      //
        state.oderChart=0;
        state.token=null;
      //
        state.patientList=null
  },
  //
  postRecodeList:(state,data)=>{
    state.recodeList=data;
  },
  //
  pushOderChart:(state,data)=>{
    state.oderChart=data
  },
  //
  postHttp:(state,list)=>{
    state.http = list;
  },
  //token
  addToken:(state,list)=>{
    state.token = list;
  },
  //  payload,  num 
  expertChartPost: (state,list) => {
    state.expertChart = list;
  },
  //
  OderingPostitle: (state,list) => {
    state.Odering = list;
    if(state.tookenM.length>0){
      for(let i in state.tookenM){
        if(list.orderNo==state.tookenM[i].id){
          state.tookenM.splice(i,1);
          return
        }
      }
    }
  },
  //
  pushMasge: (state,list) => {
    if(!state.masge){
      state.masge=[];
      state.masge.push({toAndfrm:list.toAndfrm,list:[list]});
      return
    }
    if(state.masge){
      var yAn=false;
      for(let i in state.masge){
        if(list.toAndfrm==state.masge[i].toAndfrm){
          state.masge[i].list.push(list);
          yAn=true;
          return
        }
      }
      if(!yAn){
        state.masge.push({toAndfrm:list.toAndfrm,list:[list]})
      }
    }
  },
  //
  easemobPost: (state,list) => {
    state.easemob = list;
  },
  //
  pushUser:(state,data)=>{
    state.user=data
  },
  //
  ucliFn: (state,data)=>{
    state.ucli=data
  },
  //
  ucliClereFn:(state,data)=>{
    state.ucli=null;
    state.ucliClere=data
},
  //
  pushCall:(state,data)=>{
    state.call = data
  },
  //
  pushTookenM:(state,data)=>{
    state.tookenM.push({id:data})
  },
  //
  sliceTookenM:(state,data)=>{
    for(let i in state.tookenM){
      if(state.tookenM[i].id==data){
        state.tookenM.splice(i,1);
      }
    }
  },
  //
  postPatientList:(state,data)=>{
    state.patientList=data;
  },
  //
  pushHostryMasge: (state,list) => {
    if(!state.masge){
      state.masge=[];
      state.masge.push({toAndfrm:list[0].toAndfrm,list:list});
      return
    }
    if(state.masge){
      var yAn=false;
      for(let i in state.masge){
        if(list[0].toAndfrm==state.masge[i].toAndfrm){
          state.masge[i].list=list;
          yAn=true;
          return
        }
      }
      if(!yAn){
        state.masge.push({toAndfrm:list[0].toAndfrm,list:list})
      }
    }
  },
}
export default {commitTooking}



