<template>
  <div id="app" :to="callName">
    <router-view></router-view>
    <div v-show="videoState !== 0" class="chat__video" id="im_video">
      <div class="chat__mask" v-show="videoState == 1">
        <div class="callAndOverBox" :expet="epxert">
          <div class="callAndOver" :user="users">
            <div>
              <p style="color: #fff;text-align: center"> {{contact}},</p>
            </div>
            <div style="margin-top: 20px;">
              <button type="button" class="btn btn-success  details-btn " @click="callOrOver(1)" style="float: left"> </button>
              <button type="button" class="btn btn-warning   details-btn" @click="callOrOver(2)" style="float: right"></button>
            </div>
          </div>
        </div>
      </div>
      <div class="chat__video--container">
        <div v-show="(videoState !== 3)" class="chat__video--title">
          {{contact}}
          <div class="chat__video--duration">
            <!-- 08:23 -->
          </div>
        </div>
        <video v-show="videoState === 2" :class="{'chat__video--full-ing': !chgVedioType, 'chat__video--corner-ing': chgVedioType}" id="video" autoplay=""></video>
        <div v-show="videoState === 2" :class="{'chat__video--full-ing': chgVedioType, 'chat__video--corner-ing': !chgVedioType}">
          <video class="localVideo" id="localVideo" autoplay="" @click="chgVedio"></video>
        </div>
        <div class="chat__video--img shutdown" @click="callOrOver(2)">
          <img src="../static/img/close@2x.png" alt="">
        </div>
        <div class="chat__video--img max" @click="max">
          <img src="../static/img/max@2x.png" alt="">
        </div>
        <div class="chat__video--img min" @click="min">
          <img src="../static/img/min@2x.png" alt="">
        </div>
      </div>
    </div>
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      size="tiny">
      <span>{{expertName}}</span>
      <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false"> </el-button>
    <el-button type="primary" v-on:click="goOder(oderChartData)"></el-button>
  </span>
    </el-dialog>
  </div>
</template>
<style scoped>
  /*.chat__video{width: 400px;height: 400px;position:fixed;z-index: 1000000009;right: 20px;top:70px;}*/


  /*video.block{display: block}*/

  .callAndOverBox {
    width: 250px;
    height: 60px;
    position: absolute;
    left: 50%;
    top: 50%
  }

  .callAndOver {
    width: 250px;
    height: 60px;
    position: absolute;
    left: -50%;
    top: -50%;
    z-index: 11000000000
  }

  .chat__video {
    z-index: 113;
    position: fixed;
    top: 20%;
    left: 20%;
    width: 400px;
    height: 300px;
    overflow: hidden;
    border-radius: 4px;
    background-color: #000;
  }

  .chat__video--container {
    position: relative;
    width: 100%;
    height: 100%;
  }

  .chat__video--title {
    z-index: 1000;
    position: absolute;
    top: 0px;
    height: 40px;
    padding-top: 10px;
    width: 100%;
    text-align: center;
    color: #fff;
  }

  .chat__video--duration {
    font-size: 12px;
    padding: 5px;
  }

  .chat__video--full-ing {
    z-index: 1;
    width: 100%;
    height: 100%;
    object-fit: contain;
  }

  .chat__video--corner-ing {
    position: absolute;
    background-color: silver;
    cursor: pointer;
    z-index: 400;
    right: 0px;
    bottom: 0px;
    height: 40%;
    width: 23%;
    object-fit: contain;
    overflow: hidden;
  }

  .localVideo {
    height: 100%;
    transform: translateX(-21%);
  }

  .chat__video--img {
    position: absolute;
    cursor: pointer;
    z-index: 5000;
    width: 15px;
    top: 10px;
    height: 15px;
  }

  .chat__video--img img {
    width: 100%;
    height: 100%;
  }

  .max {
    right: 35px;
  }

  .min {
    right: 60px;
  }

  .shutdown {
    right: 10px;
  }
</style>
<script>
  //import { mapGetters } from 'vuex';
  import im from './untils/im.js';
  //  import * as Types from 'untils/store/mutation-types.js';
  //  import bus from '../src/common/common.js';
  //  import WebIM from './untils/easemob.im.js';
  ///*  const WebIMs = require('easemob-websdk');*/
  import publicFn from './untils/Public.js';
  import { mapActions, mapGetters, mapState } from 'vuex'
  export default {
    name: 'app',
    created() {
      // 
      // document.onmousedown = this.listeningOperation;
      // document.onscroll = this.listeningOperation;
      // document.onkeypress = this.listeningOperation;
      // 
      this.$store.commit('postHttp', this.$http);
      if (localStorage.getItem('access_token')) {
        this.$store.commit('addToken', localStorage.getItem('access_token'));
      }
    },
    data: function() {
      return {
        //
        vdiovStatus: true,
        //
        I: 0,
        //
        toCall: null,
        //
        endType: true,
        //
        conn: WebIM.conn,
        rtcCall: {}, // 
        videoState: 0,   // 0 1 2 3.
        //
        chgVedioType: false,

        massege: [],
        message: null,
        webIMName: null,
        //
        time: { startTime: null, endTime: null },

        vedioType: true,
        //
        contact: '',
        orderId: "",
        oldPosition: {   // 
          x: '',
          y: '',
        },
        epxert: null,
        userChart: null,
        dialogVisible:false,
        expertName:"",
        oderChartData:""
      };
    },
    mounted: function() {
      //
      var that = this;
      conn.listen({
        onOpened: function(message) {          //
          //isAutoLoginfalse
          // conn.setPresence(); connisAutoLogintrue
          // conn.setPresence();
          console.log("%c [opened] ", "color: green")
        },
        onTextMessage: function(message) {
          // message.type;
          message.left = false;//
          message.status = "text";//
          message.toAndfrm = message.ext.orderId;//
          if (message.ext.status) {
            that.ApationeList(that.userChart);
            that.postOderChart(message.ext.orderId);
            return
          }
          var time = publicFn.publicLo.dataChange();
          message.time = time.ymdTiem;
          if (message.ext.orderContact) {//
            that.massege = message;
            that.contact = message.ext.orderContact;
            that.massege = message;
          }
          //vuex
          that.$store.commit('pushMasge', message);
          //
          that.$store.commit('pushTookenM', message.toAndfrm);
          //
          that.savMasge(message);
        },  //
        onEmojiMessage: function(message) {
          // WebIMEmojiWebIM.Emojiconnection
          // {type: 'emoji(txt)', data:''}
          // type='emoji'datatype='txt'data
          var data = message.data;
          for (var i = 0, l = data.length; i < l; i++) {

          }
        },   //
        onPictureMessage: function(message) {//
          message.left = false;
          message.status = "img";
          message.toAndfrm = message.ext.orderId;
          var time = publicFn.publicLo.dataChange();
          message.time = time.ymdTiem;
          //vuex
          that.$store.commit('pushMasge', message);
          //
          //that.$store.commit('pushTookenM', message.toAndfrm);
          var options = { url: message.url };
          //
          that.savMasge(message);
          options.onFileDownloadComplete = function() {
            // 
          };
          options.onFileDownloadError = function() {
            // 
            console.log('Image download failed!');
          };
          WebIM.utils.download.call(conn, options);       // 

        }, //
        onCmdMessage: function(message) {
          if (message.action) {
            that.ApationeList(that.userChart);
//            that.postOderChart(message.ext.orderId);
            if(message.action==2){
              that.oderChartFn(message.ext.orderId);
//              that.$notify({
//                title: '',
//                type:"warning",
//                message: '',
//                duration: 0
//              });
            }else {
                //
                this.$store.commit('OderingPostitle',null);
                this.$router.push({ name: 'AssistOrderIng'});
            }
          }
          console.log('CMD');
        },     //
        onAudioMessage: function(message) {
          console.log("Audio");
          that.$store.commit('pushMasge', message);
        },   //
        onLocationMessage: function(message) {
          console.log("Location");
        },//
        onFileMessage: function(message) {
          console.log("File");
          that.$store.commit('pushMasge', message);
        },    //
        onVideoMessage: function(message) {
          var node = document.getElementById('video');
          var option = {
            url: message.url,
            headers: {
              'Accept': 'audio/mp4'
            },
            onFileDownloadComplete: function(response) {
              var objectURL = WebIM.utils.parseDownloadResponse.call(conn, response);
              node.src = objectURL;
            },
            onFileDownloadError: function() {
              console.log('File down load error.')
            }
          };
          WebIM.utils.download.call(conn, option);
        },   //
        onPresence: function(message) {
          switch (message.type) {
            case 'subscribe':                           // 
              // 
              document.getElementById('agreeFriends').onclick = function(message) {
                conn.subscribed({
                  to: 'asdfghj',
                  message: "[resp:true]"
                });
                // 
                conn.subscribe({
                  to: message.from,
                  message: "[resp:true]"
                });
              };
              // 
              document.getElementById('rejectFriends').onclick = function(message) {
                conn.unsubscribed({
                  to: message.from,
                  message: "rejectAddFriend"                  // 
                });
              };
              break;
            case 'subscribed':                          // 
              break;
            case 'unsubscribe':                         // 
              break;
            case 'unsubscribed':                        // 
              break;
            case 'joinChatRoomSuccess':                 // 
              console.log('join chat room success');
              break;
            case 'joinChatRoomFaild':                   // 
              console.log('join chat room faild');
              break;
            case 'joinPublicGroupSuccess':              // 
              console.log('join public group success', message.from);
              break;
            case 'createGroupACK':
              conn.createGroupAsync({
                from: message.from,
                success: function(option) {
                  console.log('Create Group Succeed');
                }
              });
              break;
          }
        },       //
        onRoster: function(message) {

        },         //
        onInviteMessage: function(message) {

        },  //
        onOnline: function() {
          that.longHx();
        },                  //
        onOffline: function() {
          that.longHx();
        },                 //
        onError: function(message) {
          if (that.I > 9) {
            return
          }
          if (message && message.type == 1) {
            console.warn('appKey')
          } else if (message.type === 16 || message.type == 32 || message.type == 7) {//
            that.longHx();
            that.I += 1;
          } if (message.type === 8) {

          } else {
            that.longHx();
            that.I += 1;
          }
        },           //
        onBlacklistUpdate: function(list) {
          // list
          console.log(list);
        }     // 
      });
      this.linsert();//
      // this.registerUser();

      this.dragable();
    },
    methods: {
      ...mapActions({
        ApationeList: 'ApationeList',
        postOderChart: "postOderChart",
      }),
      //
      oderChartFn(id){
        var url='/api/assist-order/assistOrders/'+id;
        var token= localStorage.getItem('access_token');
        this.$http.get(url,{
          headers:{
            Authorization: 'bearer '+token
          }
        }).then(function(res){
          var res=res.body;
          this.dialogVisible=true;
          this.expertName=res.expertName;
          this.oderChartData=res
        }).catch(function (res) {
          this.dialogVisible=true;
          this.expertName=""
        });
      },
      // 
      goOder(data){
        this.dialogVisible = false;
        this.$router.push({ name: 'AssistOrderIng', query: { id: data.expertId, orderNo: data.id, paitornt: data.patientId, doctorId: data.doctorId } });
      },
      //
      longHx: function() {//
        var par = localStorage.getItem('easemob');
        var webim_ = localStorage.getItem('webim_');
        if (par.easemobUser) {
          var options = {};
          par = JSON.parse(par);
          if (webim_) {
            options = { apiUrl: WebIM.config.apiURL, user: par.name, pwd: par.password, appKey: WebIM.config.appkey };//
          } else {
            options = { apiUrl: WebIM.config.apiURL, user: par.name, accessToken: webim_, appKey: WebIM.config.appkey };//to
          }
          conn.open(options);
        }

      },
      callOrOver: function(a) {
        if (!this.vdiovStatus) {
          this.videoState = 0;
          return
        }
        if (a == 1) {
          this.videoState = 2;
          this.rtcCall.acceptCall();
        }
        if (a == 2) {
          this.videoState =0;
          this.rtcCall.endCall();
          this.$store.commit('pushCall', null);
        }
      },
      dragable: function() {
        const imVideo = document.getElementById('im_video');
        const app = document.getElementById('app');
        const position = (e) => {
          if (this.oldPosition.x && this.oldPosition.y) { // 
            const deltX = e.screenX - this.oldPosition.x;
            const deltY = e.screenY - this.oldPosition.y;
            imVideo.style.left = `${imVideo.offsetLeft + deltX}px`;
            imVideo.style.top = `${imVideo.offsetTop + deltY}px`;
          }
          this.oldPosition.x = e.screenX;
          this.oldPosition.y = e.screenY;
        };
        imVideo.addEventListener('mousedown', () => {
          imVideo.addEventListener('mousemove', position, false);
        }, false);
        app.addEventListener('mouseup', () => {
          this.oldPosition.x = '';
          this.oldPosition.y = '';
          imVideo.removeEventListener('mousemove', position, false);
        }, false);
      },
      //
      videoCall: function() {//
        this.rtcCall.caller = this.userChart.easemobUser.username,
          this.rtcCall.makeVideoCall(this.toCall);
        this.videoState = 3;
      },
      //
      acceptCall: function() {//
        this.videoState = 2;
      },
      //
      endCall: function() {//
        that.videoState = 0;
        this.rtcCall.endCall();
        this.$store.commit('pushCall', null);
      },
      //
      min: function() {
        const imVideo = document.getElementById('im_video');
        imVideo.style.top = '20%';
        imVideo.style.left = '20%';
        imVideo.style.width = '400px';
        imVideo.style.height = '300px';
      },
      max: function() {
        const imVideo = document.getElementById('im_video');
        imVideo.style.top = 0;
        imVideo.style.left = 0;
        imVideo.style.width = '100%';
        imVideo.style.height = '100%';
      },
      chgVedio: function() {
        // this.chgVedioType = !this.chgVedioType;
      },
      addContactAndMessage: function(msg) {
        //      this.$store.commit(Types.ADD_MSG, msg); // 
        //      this.$store.commit(Types.ADD_CONTACT, msg);
      },
      linsert: function() {
        this.rtcCall = null;
        var that = this;
        if (WebIM.WebRTC) {
          this.rtcCall = new WebIM.WebRTC.Call({
            connection: conn,
            mediaStreamConstaints: {
              audio: true,
              video: true
            },
            listener: {
              onAcceptCall: function(from, options) {
                that.videoState = 2; // videoState
              },
              onGotRemoteStream: function(stream, streamType) {
                console.log("onGotRemoteStream");
                var video = document.getElementById('video');
                video.srcObject = stream;
              },
              onGotLocalStream: function(stream, streamType) {
                console.log("onGotLocalStream");
                var video = document.getElementById('localVideo');
                video.srcObject = stream;
                that.videoState = 2; // videoState 
              },
              onRinging: function(caller) {
                console.log(caller, "onRinging");
                that.videoState = 1;
              },
              onTermCall: function(reason) {
                console.log('reason:', reason);
                this.videoState = 0;
              },
              onIceConnectionStateChange: function(iceState) {
                console.log(iceState, "iceState");
                if (iceState == "closed") {
                  that.videoState = 0;
                }
                if(iceState =='disconnected'){
                  that.warning("")
                }
              },
              onError: function(e) {
                console.log(e, "callee");
                that.vdiovStatus = false;
                that.$store.commit('pushCall', null);
                if (e.message == "callee is not online!") {
                  that.open("");
                }
                return true
              }
            }
          });
        } else {
          console.warn('webrtchttps')
        }
      },
      //
      savMasge: function(list) {
        if (this.userChart.userType == 2) {
          return;
        }
        var parm = {};
        var date = publicFn.publicLo.dataChange();
        date = date.ymdTiem;
        if (list.status == "text") {
          parm = {
            "createdBy": 0,
            "dialogueContent": JSON.stringify({ data: list.data, ext: list.ext }),
            "dialogueTime": "2017-08-03T12:59:58.563Z",
            "id": 0,
            "orderId": list.ext.orderId,
            "updatedBy": 0,
            "updatedWhen": "2017-08-03T12:59:58.563Z",
            "type": 1,
            "userId": 0
          }
        }
        if (list.status == "img") {
          parm = {
            "createdBy": 0,
            "dialogueContent": JSON.stringify({ data: list.url, ext: list.ext }),
            "dialogueTime": "2017-08-03T12:59:58.563Z",
            "id": 0,
            "orderId": list.ext.orderId,
            "updatedBy": 0,
            "updatedWhen": "2017-08-03T12:59:58.563Z",
            "type": 2,
            "userId": 0
          }
        }
        parm = JSON.stringify(parm);
        var url = '/api/assist-order/uDialogueRecord';
        var token = localStorage.getItem('access_token');
        this.$http.post(url, parm, {
          headers: {
            Authorization: 'bearer ' + token
          }
        }).then(function(res) {
          res = res.body;

        }).catch(function(e) {

        });
      },
      open: function(text) {
        this.$message(text);
      },
      success: function() {
        this.$message({
          message: '',
          type: 'success'
        });
      },
      warning: function(text) {
        this.$message({
          message: text,
          type: 'warning'
        });
      },
    },

    computed: {
      expertId: function() {
        this.epxert = this.$store.state.Odering;
        return this.epxert
      },
      callName: function() {
        var dat = this.$store.state.call;
        if (dat) {
          this.toCall = dat;
          this.videoCall();
        }
        return this.toCall
      },
      users: function() {
        var data = this.user;
        if (data) {
          this.userChart = data
        }
        return 1
      },
      ...mapState([
        'oderChart',
        'user'
      ]),
    },
    // 
    listeningOperation() {

    }
  }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
    margin-top: 0px;
    height: 100%;
  }
</style>



