// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VueResource from 'vue-resource';
import { store } from './untils/vuex/store.js';
import moment from 'moment' //
import ElementUi from 'element-ui'
import 'element-ui/lib/theme-default/index.css'

//import store from './untils/store/index.js';
Vue.use(VueResource);
Vue.use(ElementUi);
Vue.config.productionTip = false;
Vue.config.silent = true; //vue
Vue.config.devtools = false; //vue
import '../static/css/base.css'
// vue
Vue.use(VueResource);
Vue.config.productionTip = false;
// 
Vue.filter('diseaseFilter', function(input) {
    if (input == "asthma") {
        return ''
    } else if (input == "cardiovascular") {
        return ''
    } else if (input == "electrophysiology") {
        return ''
    }
});
// 
Vue.filter('dataFilter', function(input) {
    if (!input) { return '' }
    return moment(input).format('YYYY-MM-DD')
});

//
Vue.filter('dataTimeFilter', function(input) {
    if (!input) { return '' }
    return moment(input).format('YYYY-MM-DD HH:mm:ss')
});

//
Vue.filter('timeFilter', function(input) {
    if (!input) { return '' }
    return moment(input).format('HH:mm:ss')
});

// 
Vue.filter('moneyMes', function(input) {
    if (input == 1) {
        return ''
    } else if (input == 2) {
        return ''
    } else if (input == 3) {
        return ''
    } else if (input == 4) {
        return ''
    } else if (input == 0) {
        return ''
    }
})

new Vue({
    el: '#app',
    router,
    store: store,
    template: '<App/>',
    components: { App }
})


